import { Component, OnInit, Input } from '@angular/core';
import { sharedModuleEventHandlerType, SharedModuleEventHandlerService } from '@shared-event-handler';

@Component({
  selector: 'app-controls',
  templateUrl: './controls.component.html',
  styleUrls: ['./controls.component.scss']
})

export class ControlsComponent implements OnInit {
  @Input() data: string;
  navigationType = [];

  constructor(
    private sharedModuleEventHandlerService: SharedModuleEventHandlerService
  ) { }

  ngOnInit(): void {
    this.navigationType = this.data.split("-");
  }

  onNavAction(type: string){
    this.sharedModuleEventHandlerService.broadcast(sharedModuleEventHandlerType.CONTROL_NAVIGATION, type);
  }

}